import React from 'react';
import { Alert } from 'reactstrap';

export default class SuccessAlert extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: true
    };
  }

  render() {
    return(
      <div>
        <Alert color="success" isOpen={this.props.visible}>
          Success!  Your query has been submitted!
        </Alert>
      </div>
    );
  }
}



