import React from 'react';
import Formsy from 'formsy-react';
import FormInput from '../../components/form/form_input';
import Dropdown from '../../components/form/dropdown';
import { Container, Row, Col, Button, Modal, ModalHeader, ModalBody, ModalFooter, NavLink } from 'reactstrap';
import PhoneIcon from '../../images/icons/phone.svg';
import '../../stylesheets/contact.css';
import jquery from 'jquery';
import SuccessAlert from '../../components/alerts/success_alert';
import WarningAlert from '../../components/alerts/warning_alert';

export default class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      enableButton: false,
      visible: false,
      warningVisible: false,
      contactMethod: [
        { label: 'Contact by Phone', value: 'contactByPhone' },
        { label: 'Contact by Email', value: 'contactByEmail' }
      ]
    }
    this.toggle = this.toggle.bind(this);
    this.getContactLabel = this.getContactLabel.bind(this);
    this.disableButton = this.disableButton.bind(this);
    this.enableButton = this.enableButton.bind(this);
    this.sendContact = this.sendContact.bind(this);
    this.showSuccessAlert = this.showSuccessAlert.bind(this);
    this.showWarningAlert = this.showWarningAlert.bind(this);
  }

  enableButton() {
    this.setState({ enableButton: true });
  }

  disableButton() {
    this.setState({ enableButton: false });
  }

  getContactLabel(contactMethod) {
    return contactMethod.label
  }

  showSuccessAlert() {
    this.setState({ visible: true });
  }

  showWarningAlert() {
    this.setState({ warningVisible: true });
  }

  sendContact(data) {
    var self = this;
    var params = {
      first_name: data.first_name,
      last_name: data.last_name,
      email: data.email,
      phone: data.phone,
      contactby: data.contactby.label,
      question: data.question
    }

    jquery.ajax({
      type: 'POST',
      url: 'https://collision.holmanautomotive.com/contact_lauderdale',
      data: { contact: params },
      success: () => {
        self.setState({ visible: true, warningVisible: false });
      },
      error: () => {
        self.setState({ warningVisible: true, vislble: false });
      }
    })
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    })
  }

  render() {
    return (
      <div>
        <NavLink onClick={this.toggle}>{this.props.buttonLabel}CONTACT US</NavLink>
        <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          <ModalHeader toggle={this.toggle}>Contact Us</ModalHeader>
          <ModalBody>
            <Container fluid>
              <Row>
                <Col>
                  <div className="text-center">
                    <img src={PhoneIcon} alt="Phone" id="phone-icon" /> <a href="tel:1-954-926-3590" id="holman-link">(954) 926-3590</a>
                  </div>
                </Col>
              </Row>
            </Container>
            <Formsy onValid={this.enableButton} onInvalid={this.disableButton} onSubmit={this.sendContact}>
              <FormInput name="first_name" label="First Name" validations="minLength:2" validationError="First Name Required" required />
              <FormInput name="last_name" label="Last Name" validations="minLength:2" validationError="Last Name Required" required />
              <FormInput name="email" label="Email" validations="isEmail" validationError="Valid Email Required" required />
              <FormInput name="phone" type="phone" label="Phone" validations="isNumeric" validationError="Valid Phone Number Required" required />
              <Dropdown name="contactby" label="Preferred Contact Method" placeholder="Preferred Contact Method" options={this.state.contactMethod} validationError="Please Select a Preferred Contact Method" required />
              <FormInput componentClass="textarea" name="question" label="Questions or Comments" validationError="Question or Comment Please" required />
              <Row>
                <Col md={12}>
                  <SuccessAlert visible={this.state.visible} />
                  <WarningAlert warningVisible={this.state.warningVisible} />
                </Col>
              </Row>
              <ModalFooter>
                <Button id="holman-btn" type="submit" disabled={!this.state.enableButton}>Submit</Button>
                <Button color="secondary" onClick={this.toggle}>Close</Button>
              </ModalFooter>
            </Formsy>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}



