import React from 'react';
import { Alert } from 'reactstrap';
import AlertIcon from '../../images/icons/alert.svg';

export default class WarningAlert extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      warningVisible: true
    };
  }

  render() {
    return (
      <div>
        <Alert color="warning" isOpen={this.props.warningVisible}>
          <div className="text-center">
            <img src={AlertIcon} id="warning-icon" alt="Attention"/> Something went wrong with your request, please close this form and try again.
          </div>
        </Alert>
      </div>
    );
  }
}



